<template>
  <div class="about">
    <h1>This is an about page</h1>
    <div v-for="item in mergedArr" :key="item.id">
      ID: {{ item.id }}, Name: {{ item.name }}, Age: {{ item.age }}
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      arr1: [
        { id: 1, name: 'John' },
        { id: 2, name: 'Jane' },
        { id: 3, name: 'Bob' }
      ],
      arr2: [
        { id: 2, name: 'Jane', age: 25 },
        { id: 4, name: 'Alice', age: 30 }
      ]
    }
  },
  computed: {
    mergedArr () {
      const mergedArr = []
      for (let i = 0; i < this.arr1.length; i++) {
        const obj1 = this.arr1[i]
        for (let j = 0; j < this.arr2.length; j++) {
          const obj2 = this.arr2[j]
          if (obj1.id === obj2.id) {
            mergedArr.push({ ...obj1, age: obj2.age })
            break
          }
        }
      }
      return mergedArr
    }
  }
}
</script>
